<template>
  <div class="list" ref="wrapper">
    <div>
      <div class="area">
        <div class="title border-topbottom"></div>
        <div class="button-list">
          <div class="button-wrapper">
            <div class="button">{{this.$store.state.city.cityName}}</div>
          </div>
        </div>
      </div>
      <div class="area">
        <div class="title border-topbottom"></div>
        <div class="button-list">
          <div class="button-wrapper"
               v-for="item of hot"
               :key="item.id"
               @click="handleCityClick(item.name)"
          >
            <div class="button">{{item.name}}</div>
          </div>
        </div>
      </div>
      <div class="area"
           v-for="(item,key) of cities"
           :key="key"
           :ref="key">
        <div class="title border-topbottom">{{key}}</div>
        <div class="item-list" v-for="innerItem of item" :key="innerItem.id"
             @click="handleCityClick(innerItem.name)">
          <div class="item border-bottom">{{innerItem.name}}</div>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
    import BetterScroll from 'better-scroll';
    import { mapState, mapActions } from 'vuex';
    export default {
        name: "CityList",
      props:{
        cities:Object,
        hot:Array,
        letter:String
      },
      methods:{
        /**
         * 
          * @param city
         */
        handleCityClick (city) {
          // changeCityaction
          // -- storeaction

          // 


          // this.$store.dispatch('changeCity',city);  == 
          this.changeCity(city);

          //  ------
          this.$router.push('/');
        },
        ...mapActions(['changeCity'])

    },
      watch:{
          letter(){
            if (this.letter){
              const element = this.$refs[this.letter][0];
              this.scroll.scrollToElement(element);
            }
          }
      },

      mounted(){
        /**
         * vuedom
         * @type {BScroll}
         * 
         * this.scroll = new BScroll(this.$refs.wrapper, {click: true, tap: true });
         */
        this.scroll = new BetterScroll(this.$refs.wrapper, {click: true, tap: true });
      },

      computed:{
        fun1(){

        },
        fun:function () {

        }
      }
    }
</script>

<style scoped lang="stylus">
  @import "~@common/varibles"
  /**/
  .border-topbottom
    &:before
      border-color: #ccc
    &:after
      border-color #ccc
  .border-bottom
    &:before
      border-color: #ccc
  .list
    overflow hidden
    position absolute
    top: 1.58rem;
    left 0
    right 0
    bottom 0
    .title
      line-height .54rem
      background #eee
      padding-left .2rem
      color #666
      font-size .26rem
    .button-list
      overflow hidden
      padding .1rem .6rem .1rem .1rem
      .button-wrapper
        width 33.33%
        float left
        .button
          text-align center
          margin .1rem
          padding .1rem 0
          border-radius .06rem
          border .02rem solid #ccc
    .item-list
      .item
        height .76rem
        line-height .76rem
        color #666
        padding-left .2rem
</style>
<!--
  ---
-->



