<script>
    export default {
        name: "CityHeader"
    }
</script>

<style scoped lang="stylus">
   @import "~@common/varibles"
   .header
     position relative
     overflow hidden
     height $headerHeight
     line-height $headerHeight
     text-align center
     color #fff
     background $bgColor
     font-size .32rem
     .header-icon
       position absolute
       left 0
       top 0
       width .64rem
       text-align center
       font-size .4rem
