<template>
    <div class="icons">
        <swiper :options="swiperOption">
          <swiper-slide v-for="(page,index) of pages" :key="index">
            <div class="icon" v-for="item of page" :key="item.id">
              <div class="icon-img" @click="handleIconClick(item.desc)">
                <img class="icon-img-content" :src="item.imgUrl"/>
              </div>
              <p class="icon-desc">{{item.desc}}</p>
            </div>
          </swiper-slide>
        </swiper>
    </div>
</template>

<script>
    export default {
        name: "HomeIcons",
        props:{
          iconList:Array
        },
        data(){
          return {
            swiperOption:{
              autoplay:false,
              cancelable:true
            }
          }
        },

      methods:{
         handleIconClick(text){
           let command = {
             'action':'test',
             'des':text
           };
            latte.event(JSON.stringify(command));
         }
      },
      computed:{
          /**/
          pages(){
            const  pages = [];
            this.iconList.forEach((item,index) =>{
              const page = Math.floor(index / 8);
              if (!pages[page]) {
                pages[page] = [];
              }
              pages[page].push(item);
            });
            return pages;
          }
      }
    }
</script>

<!-- 
  http://img.alicdn.com/bao/uploaded/47/33/TB16gfTMpXXXXcMXpXXwu0bFXXX.png_80x80.jpg

  +   i 
-->
<style scoped lang="stylus">
  @import "~@common/varibles"
  .icons >>> .swiper-container
    height: 0
    padding-bottom: 50%
  .icons
    margin-top .rem
    .icon
      float left
      width 25%
      height 0
      padding-bottom 25%
      overflow hidden
      position relative
      .icon-img
        position absolute
        top 0
        left 0
        right 0
        bottom .44rem
        box-sizing border-box
        padding .1rem
        .icon-img-content
          display block
          height 100%
          margin 0 auto
      .icon-desc
        position absolute
        bottom  0
        left 0
        right 0
        height  .44rem
        line-height .44rem
        color red
        text-align center
        ellipses()
</style>



