import * as types from './mutation_types'
import Vue from "vue"
import store from "../index"

/**
 *  state
 */
export default {
  // changeCitymutation state
  // 1 state, 2 action

  /**
   *
   * @param state
   * @param city 2 ===
   *
   *   ...
   * :store.commit('changeCity',{city:''});
   *
   *  mutation 
   * store.commit({
   * type: 'changeCity',
   * amount: 10
   * })
   *
   *
   *  Mutation 
   *  mutation  Flux  linter 
   *  app  mutation 
   */
  [types.SET_CITY_NAME](state,city){
    state.cityName = city;
    try {
      localStorage.cityName = city;
    }catch (e) {}
  }
};



