import Vue from 'vue'
import Router from 'vue-router'
/* @src*/
import Home from '@/pages/home/home'
import City from '@/pages/city/City'
import Detail from '@/pages/detail/Detail'
Vue.use(Router)

/**
 * :, helloworld
 *
 * 2...
 *
 * ...  '/detail/' ;:id..
 */
export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component:Home
    },{
      path:'/city',
      name:'City',
      component:City
    },{
      path:'/detail/:id',
      name:'Detail',
      component:Detail
    }
  ]
})

