<template>
  <div>
    <div class="search">
      <input v-model="keyword" class="search-input" type="text" placeholder=""/>
    </div>
    <div class="search-content"
         ref="search"
         v-show="keyword"
    >
      <ul>
        <li class="search-item border-bottom" v-for="item of list" :key="item.id">{{item.name}}</li>
        <li class="search-item border-bottom" v-show="hasNoData">
          
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
    import BetterScroll from 'better-scroll';
    export default {
        name: "CitySearch",
      data(){
          return {
            keyword:'',
            list:[],
            timer:null
          }
      },
      props:{
        cities:Object
      },
      computed:{
        hasNoData(){
          return !this.list.length;
        }
      },
      watch:{
          keyword(){
            if (this.timer){
              clearTimeout(this.timer);
            }
            if (!this.keyword) {
              this.list = [];
              return;
            }
            this.timer = setTimeout(()=>{
                const result = [];
                for (let i in this.cities){
                  this.cities[i].forEach(
                    (value)=>{
                      if (value.spell.indexOf(this.keyword) > -1 ||
                      value.name.indexOf(this.keyword) > -1){
                        result.push(value);
                      }
                    }
                  )
                }
                this.list = result;
              },100
            )
          }
      },
      mounted(){
          this.scroll = new BetterScroll(this.$refs.search);
      }
    }
</script>

<style scoped lang="stylus">
  @import "~@common/varibles"
  .search
    height .72rem
    background $bgColor
    padding 0 .1rem
    .search-input
      /**/
      box-sizing border-box
      padding 0 .1rem
      height .62rem
      line-height .62rem
      width 100%
      text-align center
      border-radius .06rem
      color #666
  .search-content
    overflow hidden
    position absolute
    top 1.58rem
    left 0
    right 0
    bottom 0
    background #eee
    z-index 1
    .search-item
      line-height .62rem
      padding-left .2rem
      color #666
      background #fff
</style>



