<template>
    <div>
      <div class="recommend-title"></div>
      <ul v-for="item of recommendList">
        <router-link tag="li"
          :to="'/detail/'+item.id"
          class="item border-bottom" :key="item.id">
          <img class="item-img" :src="item.imgUrl">
          <div class="item-info">
            <p class="item-title">{{item.title}}</p>
            <p class="item-des">{{item.des}}</p>
            <button class="item-button"></button>
          </div>
        </router-link>
      </ul>
    </div>
</template>

<script>
    export default {
        name: "HomeRecommend",
        props:{
          recommendList:Array
        },
        data:function () {
          return {
          }
        }
    }
</script>

<style scoped lang="stylus">
    /*text-indent */
    @import "~@common/mixins"
    .recommend-title
      line-height .8rem
      background #eee
      text-indent .2rem
      margin-top .2rem
    .item
      display flex
      height 1.9rem
      overflow hidden
      flex-direction row
      .item-img
        width 1.5rem
        height 1.5rem
        padding .15rem
      .item-info
        flex 1
        padding .1rem
        /*--- ...---*/
        min-width 0
        .item-title
          line-height .54rem
          font-size .32rem
          ellipses()
        .item-des
           color #ccc
           line-height .4rem
           ellipses()
        .item-button
            background #ff9300
            padding 0 .2rem
            margin-top .1rem
            border-radius .06rem
            color #fff
</style>



